.list {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f4f4fd;
  padding: 16px;
  border-radius: 6px;
}
.items {
  padding-left: 140px;
  padding-right: 140px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 140px;
}
.text {
  font-size: 24px;
  font-family: -apple-system, system-ui, 'Segoe UI', Roboto, 'Helvetica Neue',
    Ubuntu, sans-serif;
  color: #2e2f42;
}
.btn {
  appearance: button;
  backface-visibility: hidden;
  background-color: #8e8f99;
  border-radius: 6px;
  border-width: 0;
  box-shadow: rgba(50, 50, 93, 0.1) 0 0 0 1px inset,
    rgba(50, 50, 93, 0.1) 0 2px 5px 0, rgba(0, 0, 0, 0.07) 0 1px 1px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  font-family: -apple-system, system-ui, 'Segoe UI', Roboto, 'Helvetica Neue',
    Ubuntu, sans-serif;
  font-size: 24px;
  height: 44px;
  line-height: 1.15;
  margin: 12px 0 0;
  outline: none;
  overflow: hidden;
  padding: 0 25px;
  position: relative;
  text-align: center;
  text-transform: none;
  transform: translateZ(0);
  transition: all 0.2s, box-shadow 0.08s ease-in;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  max-width: 268px;
}
